function FirstPage(props: any) {
  const ref = useRef(null);
  const [isPresent, safeToRemove] = usePresence();

  var audio = new Audio(song);

  const imageClick = () => {
    audio.play();
    audio.loop = true;
    props.setSlide(props.slide + 1);
  };

  useEffect(() => {
    if (!isPresent) {
      gsap.to(ref.current, {
        opacity: 0,
        scale: 2,
        onComplete: () => safeToRemove?.(),
      });
    }
  }, [isPresent, safeToRemove]);

  return (
    <div ref={ref} className="centered">
      <img
        src={face}
        onClick={() => imageClick()}
        className="App-logo"
        alt="logo"
      />
      <motion.div
        initial={{ opacity: 0, scale: 1 }}
        animate={{ opacity: 1, scale: 1 }}
        transition={{
          repeat: Infinity,
          repeatType: "mirror",
          duration: 2,
          delay: 0,
          ease: [0, 0.71, 0.2, 1.01],
        }}
      >
        <p className="elementToFadeInAndOut">Click your stupid face.</p>
      </motion.div>
    </div>
  );
}

